import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomContactStatisticsComponent } from './custom-contact-statistics/custom-contact-statistics.component';
import { CustomContactListComponent } from './custom-contact-list/custom-contact-list.component';
// import { RelaseSpeedTarckDetailsComponent } from './custom-contact-task/relase-speed-tarck/relase-speed-tarck-details/relase-speed-tarck-details.component';
import { CustomContactHistoryComponent } from './custom-contact-history/custom-contact-history.component';
import { CustomContactApprovalComponent } from './custom-contact-approval/custom-contact-approval.component';
import { CustomContactListHandleComponent } from './custom-contact-list/custom-contact-list-handle/custom-contact-list-handle.component';
import { CustomContactAddPlanComponent } from './custom-contact-add-plan/custom-contact-add-plan.component';
import { CustomContactAddMorePlanComponent } from './custom-contact-add-morePlan/custom-contact-add-morePlan.component';
import { CustomContactNewComponent } from './custom-contact-new.component';
import { RelaseSpeedTarckComponent } from './custom-contact-task/relase-speed-tarck/relase-speed-tarck.component';
//客户群组

export const routes: Routes = [
    {
        path: '',
        component: CustomContactNewComponent,
        children: [
            // {
            //     path: '', redirectTo: 'custom-contact-new', pathMatch: 'full'
            // },
            { path: 'custom-contact-statistics', component: CustomContactStatisticsComponent },//客戶联系成效统计
            { path: 'custom-contact-list', component: CustomContactListComponent },//客戶联系清单
            { path: 'custom-contact-task', loadChildren: './custom-contact-task/custom-contact-task.module#CustomContactTaskModule' },//发布客户联系任务
            // { path: 'relase-speed-tarck-details', component: RelaseSpeedTarckDetailsComponent },//任务进度跟踪详情
            { path: 'custom-contact-history', component: CustomContactHistoryComponent },//客戶联络历史
            { path: 'custom-contact-add-plan', component:  CustomContactAddPlanComponent},// 新建客户联系计划
            { path: 'custom-contact-add-morePlan', component:  CustomContactAddMorePlanComponent},// 批量新建客户联系计划
            { path: 'custom-contact-approval', component:  CustomContactApprovalComponent},// 审批页面
            { path: 'custom-contact-list-handle', component:  CustomContactListHandleComponent},// 客户联系清单处理
            // { path: 'relase-speed-tarck', component:  RelaseSpeedTarckComponent},// 任务进度跟踪
        ]
    },
]

export const CustomContactNewRouting: ModuleWithProviders = RouterModule.forChild(routes);
